import React, { PureComponent } from "react";
import { DetailWrapper, Header, Content } from "../detail/style";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { actionCreator } from "./store";

class Detail_3 extends PureComponent{
  render(){
    // Destructuring
    const { list } = this.props;
    return(
      <DetailWrapper key = {list.get("id")}>
        <Header>{list.get("title")}</Header>
        <Content dangerouslySetInnerHTML = {{__html: list.get("content")}}/>
      </DetailWrapper>
    );
  }

  componentDidMount(){
    this.props.getDetail(this.props.match.params.id);
  }
}

const mapStateToProps = (state) => ({
  list: state.getIn(["detail", "list"])
})  

const mapDispatchToProps = (dispatch) => ({
  getDetail(id){
    dispatch(actionCreator.getDetail(id));
  }
})  

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Detail_3));