import { actionTypes } from "../../detail/store";
import axios from "axios";

export const getDetail = (id) => {
  return (dispatch) => {
    axios.get(`/api/detailList.json?id={id}`).then((res) => {
      const result = res.data.data[2];
      dispatch(changeDetailData(result));
    }).catch(() => {
      console.log("failed");
    })
  }
};

const changeDetailData = (result) => ({
  type: actionTypes.CHANGE_DETAIL_DATA,
  list: result
});
